.App {
  text-align: center;
}

.App-logo {
  height: 100px;
  pointer-events: none;
  margin-top: 0%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.input{
  width: 500px;
  height: 30px;
  border-radius: 10px;
}
.mdp{
  margin-right: 115px;
}
.input2{
  width: 500px;
  height: 30px;
  border-radius: 10px;
}

.button{
  width: 100px;
  height: 50px;
  background-color: darkcyan;
  color: rgb(255, 255, 255);
  border-radius: 10;
}




@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


